// Instructions:
// Media Queries are located on the bottom of the page, for fine tuning. Margins and width and 
// relative font sizes are declared with mixins

.teaser {
    display: inline-block;
}

.heading {

    .text--bolder {
        @include fontSize(45,20,32);
        @include lineHeight(48,24,48);
        @include letterSpacing(0,0,0);    
    }

    .text--italic {
        @include fontSize(50,20,30);
        @include lineHeight(38,24.52,48);
        @include letterSpacing(0,0,0);
        padding-left: 0.5rem;    
    }

    .text--regular {
        @include fontSize(40,20,30);
        @include lineHeight(48,24.52,48);
        @include letterSpacing(50,50,50);
    }

}

// Sub Menu Top Articles Teasers 

.sub-menu__first-teaser,
.sub-menu__second-teaser {
    @include width(0,0,234);
    @include marginTop(0,0,18);
    position: relative;

    &__caption {
        width: 15rem;
        position: absolute;
        right: 0;
        top: 0;

        h4 {
            margin: 0;
        }
    }
    
}

