.quiz-teaser {
    @include marginTop(81,76,113);
    @include marginBottom(90,120,212);

    .container {
        width: 100%;
    }
    &__wrapper {
        background: center / cover no-repeat url("../../assets/image/quiz__bg-image--mob.jpg");
    }
    &__text-content {
        padding: 7.3rem 8rem 4.2rem;
        text-align: center;
    }
    &__heading {
        h2{
            @include fontSize(46,30,32);
        }
    }
    &__text {
        margin: 4rem 0 4.9rem;
        @include fontSize(32,26,14);
        @include lineHeight(32,32,18);
        @include letterSpacing(50,50,50);
        color: $black;
    }
    &__button {

        .button--white-bg {
            @include fontSize(18,10,12);
