}

.les-plus__teaser-hidden {
    display: none;
}



////////////////////////////////////
/////// Media Queries /////////////
//////////////////////////////////


@include breakpoint(tabletAndDesktop) {
    
    .teaser {
        float: left;
    }

    .news-de-la__heading {
        margin-top: -2rem;
        position: absolute;

        .text--bolder {
            display: inline;
        }

        .text--italic {
            display: inline;
            padding-left: 0.5rem;
        }
    }

    //Fan De Vu

    .fan-de-vu {
        text-align: center;

    &__first-teaser {
        float: none;
            
        &-white-background {
            display: inline-block;
            box-sizing: border-box;
            width: 18rem;
            background: white;
            padding: 4.5vw 4vw 0 5vw;
            text-align: left;

            @include breakpoint(desktopOnly) {
                width: 35rem;
            }
        }
    }
}

    .fan-de-vu__second-teaser,
    .fan-de-vu__third-teaser ,
    .fan-de-vu__fourth-teaser  {
        float: right;
        text-align: left;
    }

    .vu-astuces__fifth-teaser {
        margin-top: 2.6rem;
    }

    .quiz-teaser {

        .container {
            width: 64.7rem;

            @include breakpoint(desktopOnly) {
                width: 135.5rem;
            }
        }

        &__wrapper {
            @include teaser__figureResponsive(url("../../assets/image/quiz__bg-image.jpg"),
            0,272,588);
        }

        &__text-content {
            text-align: center;
            padding-top: 4.2rem;
            @include breakpoint(desktopOnly) {
                padding: 10rem 52rem 23rem;
                text-align: left;
            }
        }

        &__text {
            display: none;
            @include breakpoint(desktopOnly) {
                display: block;
                margin: 0;
            }
        }

        &__button {
            margin-top: 2rem;

            .button--white-bg {
                padding: 1rem 5rem;
            }
        }

    }

    .les-plus__teaser-hidden {
        display: inline-block;
    }
}